<script>
let api = new URL("http://alhost:8081");
api.pathname = "ventas";

//api.port = 8000;
//api.port = 8081;

import VueHtml2pdf from "vue-html2pdf";
import { mapState, mapActions } from "vuex";
import axios from "axios";
import "jspdf/dist/polyfills.es.js";
import { jsPDF } from "jspdf";
import ReporteListadopdf from "./ReporteClientesListadopdf.vue";
import Sortable, { MultiDrag, Swap } from 'sortablejs';
import Buefy from 'buefy';
import html2pdf from 'html2pdf.js'
import Chart from 'chart.js/auto';
import { getRelativePosition } from 'chart.js/helpers';
import { APIControler } from "@/store/APIControler";
import ElTableDraggable from 'el-table-draggable'
//Vue.use(Buefy);

const createSortable = (el, options, vnode) => {
  
  return Sortable.create(el, {
    ...options
  });
};

const sortable = {
  name: 'sortable',
  bind(el, binding, vnode) {
    const table = el.querySelector('table');
    table._sortable = createSortable(table.querySelector('tbody'), binding.value, vnode);
  }
};

export default {
  directives: { sortable },
  components: { VueHtml2pdf, ReporteListadopdf},
  data() {
    return {
      productoslista: [],
      reparto: {},
      chart: null,
      chart2: null,
    chart3: null,
    chart4: null,
      datarep: [],
      listacli: [],
      hex:[],
      idprodus: -1,
      lista_id: [],
        productousado: {
            produ: {},
            tabla_ventas: [],
        },
      productos: [],
      datachart: {
            labels: [
            ],
            datasets: []
        },
        datachart4: {
            labels: [
            ],
            datasets: []
        },
      datachart3: {
            labels: ['Congelados', 'Pollo'],
            datasets: []
      },
      datachart2: {
            labels: [],
            datasets: []
      },
      fields: [
          { key: "produ.id_producto", label: "Id Producto", sortable: true },
          { key: "produ.nombre", label: "Nombre/s", sortable: true },
          { key: "produ.precioref", label: "Precio Ref", sortable: true },
          { key: "precioprom", label: "Pro Venta", sortable: true },
          { key: "totalvendido", label: "Cantidad Vendida", sortable: true },
          { key: "recaudado", label: "Venta Total", sortable: true },
          { key: "costototal", label: "Costo Total", sortable: true },
          { key: "produ.tipo_producto", label: "Tipo", sortable: true },
          { key: "nroventas", label: "Numero de Ventas", sortable: true },
          { key: "ganancia", label: "Ganancia", sortable: true },
          { key: "action", label: "", variant: "secondary" },
          //{ key: 'yavisitado', label: 'Ya Visitado', sortable: true, },
          //{ key: "action", label: "Acciones", variant: "secondary" },
      ],
      totalRows: 1, //Total de filas
      currentPage: 1, //Pagina actual
      perPage: 10, // Datos en la tabla por pagina
      buscar: "",
      fechas: [],
        op_repartos: [],
        op_productos: [],
        fechainiusada: null,
        fechafinusada: null,
      sortableOptions: {
        chosenClass: 'is-selected'
      },
      token: "",
        infoVisitar: {
        id: "modal_visitar",
        cliente: -1,
      },
      ordenAPDF: {}, //Se carga cuando se hace clic en exportar a pdf, con la orden a exportar
    };
  },
  computed: {
    rows() {
      return this.tabla_ventas.length;
    },
    id() {
        return this.tabla_ventas.numero_orden;
    },
    items() {
      return tabla_ordenes.filter((item) => {
        return item.numero_orden
          .toLowerCase()
          .includes(this.buscar.toLowerCase());
      });
    },
  },
  methods: {
    suma(ventas)
    {
          let suma = 0;
          for (var i = 0; i < ventas.length; i++)
          {
              suma += ventas[i].totalpagado + ventas[i].totalpagadomp;
          }
          return suma;
    },
    costos(ventas) {
        let costotot = 0;
        ventas.forEach((venta) => {
            costotot = costotot + venta.costototal
        });
        return costotot;
    },
    ganancia(ventas) {
        let costotot = 0;
        ventas.forEach((venta) => {
            costotot = costotot + venta.ganancia
        });
        return costotot;
    },
    stockinicial(item) {
        let stockfinal = item.produ.stocktotal;
        item.tabla_ventas.forEach((venta) =>
        {
            venta.componentes.forEach((compon) => {
                if (parseInt(compon.producto.split('/')[4]) == item.produ.id_producto)
                {
                    stockfinal = stockfinal + compon.cantidad;
                }
            })
        })
        return stockfinal;
    },
    cantidadvendida(item) {
        let total = 0;
        item.tabla_ventas.forEach((venta) => {
            venta.componentes.forEach((compon) => {
                if (parseInt(compon.producto.split('/')[4]) == item.produ.id_producto) {
                    total = total + compon.cantidad;
                }
            })
        })
        return total;
    },
    proventas(item) {
        let totalprecio = 0;
        item.tabla_ventas.forEach((venta) => {
            venta.componentes.forEach((compon) => {
                if (parseInt(compon.producto.split('/')[4]) == item.produ.id_producto) {
                    totalprecio = totalprecio + compon.precio;
                }
            })
        })
        let promedio = item.produ.precioref;
        if (item.tabla_ventas.length != 0)
        {
            promedio = totalprecio / item.tabla_ventas.length
        }
        return promedio;
    },
    fechausadacambio()
    {
        this.setGraph();
        this.testFetch();
        this.fechaperiodo();
    },
    fechaperiodo()
    {
        this.fechas = [];
        let fechaini = new Date(this.fechainiusada)
        let fechafin = new Date(this.fechafinusada)
        console.log(fechaini.getDay());
        while (fechaini.getTime() <= fechafin.getTime()) {
            this.fechas.push(('0' + fechaini.getDate()).slice(-2) + "-" + ('0' + (fechaini.getMonth() + 1)).slice(-2) + "-" + fechaini.getFullYear());
            fechaini.setDate(fechaini.getDate() + 1);
        }
        console.log(this.fechas);
    },
    cambiarprodusado(id)
    {
        this.productousado = this.productoslista.find(s => {
            return s.produ.id_producto == id
        });
        this.armarGrafico();
    },
    armarGrafico()
    {
        this.datachart2.datasets = [];
        this.datachart2.labels = this.fechas;
        let datasetear1 = {
            label: 'Precio Ref',
            data: [],
            borderColor: 'rgb(255, 165, 0)',
            backgroundColor: 'rgb(255, 165, 0)',
            hoverOffset: 4,
            yAxisID: 'y',
        };
        let datasetear2 = {
            label: 'Precio Real',
            data: [],
            borderColor: 'rgb(54, 162, 235)',
            backgroundColor: 'rgb(54, 162, 235)',
            hoverOffset: 4,
            yAxisID1: 'y',
        };
        this.fechas.forEach((fecha) =>
        {
            let dia = fecha.split('-')[0];
            let mes = fecha.split('-')[1];
            let anio = fecha.split('-')[2];
            let costoprod = 0;
            let ventafecha = [];
            this.productousado.tabla_ventas.forEach((s) => {
                if (s.fecharealizacion == (anio + '-' + mes + '-' + dia)) {
                    ventafecha.push(s);
                }
            });
            if (ventafecha.length != 0) {
                let preciorefprom = 0;
                let precioprom = 0;
                ventafecha.forEach((venta) =>
                {
                    venta.componentes.forEach((compon) => {
                        if (parseInt(compon.producto.split('/')[4]) == this.productousado.produ.id_producto) {
                            preciorefprom = preciorefprom + compon.precioref;
                            precioprom = precioprom + compon.precio;
                        }
                    })
                })
                preciorefprom = preciorefprom / ventafecha.length;
                precioprom = precioprom / ventafecha.length;
                datasetear2.data.push(precioprom);
                datasetear1.data.push(preciorefprom);
            } else
            {
                datasetear2.data.push(datasetear2.data[datasetear2.data.length - 1]);
                datasetear1.data.push(datasetear1.data[datasetear1.data.length - 1]);
            }
        });
        this.datachart2.datasets.push(datasetear1);
        this.datachart2.datasets.push(datasetear2);
        let ctx2 = document.getElementById('chart2');
        if (this.chart2) {
            this.chart2.destroy();
        }
        this.chart2 = new Chart(ctx2, {
            type: 'line',
            data: this.datachart2,
            options: {
                title: {
                    text: "Cantidad de ventas por Reparto",
                    display: true
                }
            },
            interaction: {
                mode: 'index',
                intersect: false,
            },
        });
    },
    rowClass(item, type) {
        if (!item || type !== 'row')
        {
            return 'table-danger'
        }
        let maximovendido = 0;
        this.productoslista.forEach((element) =>
        {
            let cantvendida = this.cantidadvendida(element);
            if (cantvendida > maximovendido)
            {
                maximovendido = cantvendida;
            }
        })
        if (this.cantidadvendida(item) == maximovendido)
        {
            return 'table-success'
        }
    },
